.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  max-width: 1366px;
  margin: 0 auto;
  color: $color-default-black;
  background-color: $color-default-white;
  overflow-x: hidden;

  &::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    min-height: 657.5px;
    background-image: url("../img/hero-gradient.png");
    background-size: 100% 657.5px;
    background-repeat: no-repeat;
    z-index: 0;

    @include retina {
      background-image: url("../img/hero-gradient@2x.png")
    }

    @include vp-1199 {
      min-height: 554px;
    }

    @include vp-767 {
      min-height: 491px;
    }
  }
}

.page__header {
  padding: 30px 101px 30px 103px;
  min-height: 104px;
  margin: 0;
  z-index: 1;

  @include vp-1199 {
    min-height: 82px;
    padding: 25px 45px 25px 45px;
  }

  @include vp-767 {
    min-height: 54px;
    padding: 14px 15px 18px 15px;
  }
}


.page__main {
  padding: 0 101px 0 103px;
  display: flex;
  flex-direction: column;
  z-index: 1;
  margin: 0;

  @include vp-1199 {
    min-height: 82px;
    padding: 0 45px;
  }

  @include vp-767 {
    min-height: 54px;
    padding: 0 15px;
  }
}

.page__footer {
  padding: 41px 101px 22px 103px;
  min-height: 108px;
  margin: 0;

  @include vp-1199 {
    min-height: 114px;
    padding: 45px 45px 0 45px;
  }

  @include vp-767 {
    min-height: 260px;
    padding: 44px 15px 47px 15px;
  }
}
